.todoChecktasksContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    gap: 10px;
    padding: 10px;
    list-style: none;
    background-color: white;
    border-radius: 5px;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
}

.todoChecktasks{
    margin: 0px;
    width: 24px;
    height: 20px;
    border-radius: 50%;
    vertical-align: middle;
    border: 3px solid rgba(29,51,75,1);
    appearance: none;
    -webkit-appearance: none;
    outline: none;
    cursor: pointer;
}

.todoChecktasks:checked{
    background: linear-gradient(90deg, rgba(29,51,75,1) 9%, rgba(0,212,255,1) 100%);
    background-repeat: no-repeat;
    background-size: cover;
    text-decoration: underline;
}

.todoChecktasksLabel{
    margin: 0px;
    vertical-align: middle;
    width: 100%;
    max-width: 500px;
    overflow-x: scroll;
    scrollbar-width: thin;
    scrollbar-color: light;
    scrollbar-color: #142850 #e0e0e0;
    padding-bottom: 8px;
}

.todoChecktasksClose{
    margin: 0px;
    width: 30px;
    height: 35px;
    /* background-color: rgb(255, 200, 200); */
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    position: relative;
    right: -15px;
    cursor: pointer;
    fill: rgb(255, 200, 200);
}

.todoChecktasksClose:hover{
    fill: rgb(255, 125, 125);
}

.check_active{
    text-decoration: line-through;
}



@media (max-width: 670px) {
  
    .todoChecktasksLabel{
        max-width: 480px;
        overflow-x: scroll;
        padding-bottom: 8px;

    }
  
}


