.label_searchTask{
    width: 80%;
    margin-top: 20px;
    height: 40px;
    border-radius: 5px;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.2);
    background: rgb(29,51,75);
    background: linear-gradient(100deg, rgba(0,212,255,1) 2%, rgba(29,51,75,1) 3%); 
    display: flex;
    place-self: center;
}

.input_searchTask{
    width: 100%;
    height: 95%;
    border: none;
    background: none;
    text-indent: 10px;
    font-size: 15px;
    color: white;
    text-indent: 10px;
    border-radius: 5px;
}

.input_searchTask::placeholder{
    color: white;
    font-weight: bold;
}

.input_searchTask:focus{
    outline: none;
}

.icon_searchTask{
    padding-right: 10px;
    width: 30px;
    color: #0C7B93;
    text-shadow: 1px 1px 12px rgba(0, 0, 0, 0.2);
}



@media (max-width: 670px) {
  
    .label_searchTask{
        min-height: 40px;
    }
  
  }